@import "../config/variables";

.form-component {
    display: flex;
    flex-direction: column;
    .form {
        display: flex;
        flex-direction: column;
        .text-input {
            border: 1px solid #efefef;
            border-radius: 3px;
        }
    }
    .divider {
        text-transform: uppercase;
        color: $dark-grey;
        font-weight: 600;
        margin: 20px 0;
        margin-bottom: 20px;
        position: relative;
        &:before {
            content: "";
            width: 42%;
            position: absolute;
            background-color: $dark-grey;
            height: 1px;
            left: 0;
            top: 6px;
            opacity: 0.8;
        }
        &:after {
            content: "";
            width: 42%;
            position: absolute;
            background-color: $dark-grey;
            height: 1px;
            right: 0;
            top: 6px;
            opacity: 0.8;
        }
    }
    .facebook-link {
        align-items: center;
        color: #385185;
        justify-content: center;
        margin-bottom: 25px;
        cursor: pointer;
        background-color: white;
        border: 0;
        font-size: $normal-font-size;
        text-align: center;
        i {
            margin-right: 5px;
        }
        &:focus,
        &:active {
            outline: none;
        }
    }
}
