@import "../../config/variables";

.auth {
    width: 100%;
    max-width: 100%;
    margin: 0 auto;
    display: flex;
    min-height: 100%;
    margin-top: 30px;
    justify-content: center;
    align-items: center;
    .column {
      width: 40%;
      &:last-child {
        width: 35%;
        margin-left: 35px;
      }
      .app-box {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 35px;
        .appstores {
          margin-top: 25px;
          img {
            height: 40px;
            &:first-child {
              margin-right: 10px;
            }
          }
        }
      }
      .white-box {
        text-align: center;
        padding: 15px;
        @include boxBorder(no);
        .change-link {
          color: $light-blue;
          cursor: pointer;
        }
        p {
          margin: 15px;
        }
      }
      .form-box {
        margin-bottom: 10px;
      }
    }
  }
