@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:400,500);
body{background-color:#f6f6f6;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", 'Roboto Mono', Helvetica, Arial, sans-serif}

.footer{max-width:300px;width:100%;margin:0 auto;display:flex;align-items:center;justify-content:space-between;font-weight:600;text-transform:uppercase;font-size:12px;margin-top:55px;margin-bottom:55px;flex-wrap:wrap}@media (max-width: 37.5em){.footer{justify-content:center}}.footer .column{margin-bottom:15px}@media (max-width: 37.5em){.footer .column{margin-bottom:0}}.footer .column .nav .list{display:flex;flex-wrap:wrap}@media (max-width: 37.5em){.footer .column .nav .list{justify-content:center}}.footer .column .nav .list .listitem{margin-left:20px;color:#222831;cursor:pointer}@media (max-width: 37.5em){.footer .column .nav .list .listitem{margin-bottom:15px}}.footer .column .copyright{color:#393e46}

.auth{width:100%;max-width:100%;margin:0 auto;display:flex;min-height:100%;margin-top:30px;justify-content:center;align-items:center}.auth .column{width:40%}.auth .column:last-child{width:35%;margin-left:35px}.auth .column .app-box{display:flex;flex-direction:column;align-items:center;margin-top:35px}.auth .column .app-box .appstores{margin-top:25px}.auth .column .app-box .appstores img{height:40px}.auth .column .app-box .appstores img:first-child{margin-right:10px}.auth .column .white-box{text-align:center;padding:15px;border:1px solid #e6e6e6;background-color:white}.auth .column .white-box .change-link{color:#3897f0;cursor:pointer}.auth .column .white-box p{margin:15px}.auth .column .form-box{margin-bottom:10px}

.form-component{display:flex;flex-direction:column}.form-component .form{display:flex;flex-direction:column}.form-component .form .text-input{border:1px solid #efefef;border-radius:3px}.form-component .divider{text-transform:uppercase;color:#393e46;font-weight:600;margin:20px 0;margin-bottom:20px;position:relative}.form-component .divider:before{content:"";width:42%;position:absolute;background-color:#393e46;height:1px;left:0;top:6px;opacity:0.8}.form-component .divider:after{content:"";width:42%;position:absolute;background-color:#393e46;height:1px;right:0;top:6px;opacity:0.8}.form-component .facebook-link{align-items:center;color:#385185;justify-content:center;margin-bottom:25px;cursor:pointer;background-color:white;border:0;font-size:16px;text-align:center}.form-component .facebook-link i{margin-right:5px}.form-component .facebook-link:focus,.form-component .facebook-link:active{outline:none}

body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto Mono', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

